.layout {
  display: grid;
  column-gap: 24px;
  /* custom-scroll  6px  1063 - 6 = 1057 */
  grid-template-columns: 193px 1057px;
  grid-template-rows: auto 1fr auto; /*  ,    */
  height: 100vh; /*    */
  width: 100%;
}

.header {
  grid-column: 1 / -1; /*       */
}

.sidebar {
  grid-column: 1; /*     */
  grid-row: 2; /*     */
  background-color: var(--white-color);
}

.main{
  max-width: 1057px;
  grid-column: 2; /*      */
  grid-row: 2; /*      */
}

.footer {
  grid-column: 1 / -1; /*       */
  grid-row: 3; /*      */
}
